<template>
 <div class="container">
        <div class="row justify-content-center">
            <div class="col-md-8">
                <div class="card">
                    <div class="card-header">Usuarios registrados</div>
                    <pre>
                        {{users}}
                    </pre>
                    <div class="card-body">
                        <button class="btn btn-primary" v-on:click="autoLogin()">Loggear</button>
                        Esto son los usuarios registrados
                        <form  class="form-group">
                            <label for="nombre">Nombre</label>
                            <input v-model="form.name" type="text" class="form-control" id="nombre" required name="nombre">
                             <label for="correo">Correo</label>
                            <input v-model="form.email" type="email" class ="form-control" id="correo" required name="correo">
                             <label for="contraseña">Contraseña </label>
                            <input v-model="form.password" type="password" class="form-control" id="contraseña" required name="contraseña">
                            <button class="btn btn-primary" v-on:click.prevent="loginUser()">Loggear</button>
                            <button class="btn btn-primary" v-on:click.prevent="createUser()">Crear User</button>

                        </form>
                        <table class="table">
                            <thead>
                                <th>ID</th>
                                <th>Correo</th>
                                <th>Acciones</th>
                            </thead>
                            <tbody>
                                <tr v-for="user in users" :key="user.id" >
                                    <th>{{user.id}}</th>
                                    <th>{{user.name}}</th>
                                    <th><button class="btn btn-primary" v-on:click.prevent="deleteUser(user.id)">Crear</button></th>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MainChartExample from './charts/MainChartExample'
import WidgetsDropdown from './widgets/WidgetsDropdown'
import WidgetsBrand from './widgets/WidgetsBrand'

export default {
  name: 'Dashboard',
  components: {
    MainChartExample,
    WidgetsDropdown,
    WidgetsBrand
  },
  
   mounted() {
            this.getUsers();
        },
        data: function(){
            return{
                users:{
                    
                },
                form:{
                   
                }
            }
        },
        methods:{
            getUsers: function(){
                var self = this;
                axios.get('http://127.0.0.1:8000/api/usuarios').then(function (response) {
                    self.users = response.data;
                    
                });

            },
               token: function(){
                var self = this;
                axios.get('http://127.0.0.1:8000/token').then(function (response) {
                    console.log(response.data);
                    
                });

            },
            deleteUser: function (user){
                var self = this;

                console.log(user);
                axios.delete('http://127.0.0.1:8000/api/usuarios/'+user).then(function(response){
                    self.getUsers();
                });
            },
            createUser: function (){
                var self = this;

                axios.post('http://127.0.0.1:8000/api/usuarios/',this.form).then(function(response){
                    console.log(response);
                    self.getUsers();

                });
            },
            loginUser: function (){
                var self = this;

                axios.post('http://127.0.0.1:8000/api/auth/login/',this.form).then(function(response){
                    self.users = response.data;                                      
                    localStorage.token = response.data.access_token;

                });
            },
            autoLogin: function (){
                var self = this;
                console.log(self.users.access_token);
                axios.defaults.headers.common = {'Authorization': `bearer ${localStorage.token}`}

              
                
                axios.post('http://127.0.0.1:8000/api/auth/me').then(function(response){
                    console.log(response);
                                      

                }) .catch(function (err) {
              console.log(err);
            });;
            }
        }
  
}
</script>
